const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			cityInfo: {},
			see_sticky: '',
			list: [],
			page: '1',
			pageList: '1',
			tipList: [],
			total: '',
			is_follow: '',
			token: '',
			inputValue: '',
			userInfo: {}
		};
	},
	onLoad(e) {
		console.log(e);
		this.imgUrl = helper.imgUrl;
		this.token = uni.getStorageSync('token')
		uni.setNavigationBarTitle({
			title: e.title
		});
		this.cityInfo = e;
		this.getBordList()
		this.getSee_stickyList()
		this.user()
	},
	methods: {
		user(){
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					this.userInfo = res.data.id
					console.log(this.userInfo,'+++++---------------------+++')
				}
			})
		},
		// 发布
		getPost() {
			uni.navigateTo({
				url: `/pages/post?id=${38}&type=${'网友互动'}`
			});
		},
		// 置顶加载更多
		getMore() {
			this.page++;
			this.getSee_stickyList()
		},
		onReachBottom(){
			this.pageList++;
			this.getBordList()
		},
		// 跳转帖子详情
		// getDateil(e) {
		// 	uni.navigateTo({
		// 		url: `/pages/articleDetail?id=${e}`
		// 	});
		// },
		inputV(e){
			console.log(e)
			this.inputValue = e.detail.value
		},
		postContent(){
			if(this.inputValue == ''){
				uni.showToast({
					title: '请输入发表内容',
					icon: 'none'
				})
				return false
			}
			uni.request({
				url: `${helper.requestUrl}/message/sendMessageWithCityId`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					city_id: '0',
					content: this.inputValue
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '**************');
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
						this.list = []
						this.pageList = '1'
						this.inputValue = ''
						this.getBordList()
					}
				}
			});
		},
		getBordList() {
			uni.request({
				url: `${helper.requestUrl}/message/getMessageByCityId`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					city_id: 'all',
					page: this.pageList,
					page_size: '10'
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						console.log(res.data.data.data,'/////////////////////////////////')
						this.list = this.list.concat(res.data.data.data);
						this.is_follow = res.data.is_follow
					}
				}
			});
		},
		// 网友互动
		getSee_stickyList() {
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					board_id: this.cityInfo.id,
					bank_id: '0',
					child_id: '0',
					see_sticky: '1', //置顶
					page: this.page,
					page_size: '3',
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						this.total = res.data.total;
						this.tipList = this.tipList.concat(res.data.data);
					}
				}
			});
		},
		addFollow(type) {
			// 关注用户
			uni.request({
				url: `${helper.requestUrl}/user/add_follow`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					follow_id: this.cityInfo.id,
					type: type
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
						this.is_follow = 1
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
